.my-light {
    background-color: #999898;
    color: #ffffff;
}


.my-dark {
    background-color: #ffffff;
    color: #000000;
}

.theme-btn {
    height: 15px;
    width: 15px;
    border-radius: 50%;
}

.theme-green {
    border: 1px solid green;
    background-color: green;
}

.theme-blue {
    border: 1px solid red;
    background-color: red;
}
